export default function TestAnswer (props) {
    return (
        <div className="col-md-4 col-lg-4 col-sm-4">
            <label>
            <input type="radio" name={"question-" + props.id} className="card-input-element" />

                <div className="card card-default card-input">
                <div className="card-body text-center">
                    {props.text}
                </div>
                </div>
            </label>
        </div>
    );
}